

@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

* > *{
    font-family: 'Poppins', sans-serif;
}



body{
    background: #dcdce6;
    background-image: url('./assets/Background.png');
}
.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  width: 98%;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #f1356d;
  font-size: 25px;
}
.navbar .links {
  margin-left: auto;
}

.navbar .dropdown {
  margin-left: auto;
  margin-right: 20px;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  color: #333;
  font-weight: 700;
}
.navbar a:hover {
  color: #f1356d;
}

/* banner section */

.banner img {
  width: 100%;
  max-height: 150px;
}

.content {
  width: 100%;
  margin: 20px auto;
  padding: 20px;
  min-height: 100vh;
}

/* profile previews / list */
.artist-preview {
  padding: 10px 16px;
  margin: 20px 0;
  border-bottom: 1px solid #fafafa;
}
.artist-preview:hover {
  box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}
.artist-preview h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 8px;
}
.artist-preview a {
  text-decoration: none;
}

/* profile details page */
.artist-details h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 10px;
}
.artist-details div {
  margin: 20px 0;
}
.artist-details button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

/* create new profile form */
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
  position: absolute;
  top: 60%;
  left: 50%;
}

.create label {
  text-align: left;
  display: block;
}
.craete-head {
  text-align: center;
  color: #f1356d;
  margin-bottom: 30px;
  margin-top: 10px;
}
.create input,
.create textarea,
.create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}

/* dropdown styling */

.dropbtn {
  background-color: #f1356d;
  color: white;
  padding: 14px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  border-radius: 10px;
}

.dropbtn:hover,
.dropbtn:focus {
  background-color: #f1356d96;
}
.dropdown {
  position: relative;
  display: inline-block;
}
.dropdown-content {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 100px;
  overflow: auto;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  justify-content: center;
}
.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
.dropdown a:hover {
  background-color: #ddd;
}

.show {
  display: block;
}
/* this is footer section */
.footer-container {
  width: 100%;
  min-height: 100px;
  background-color: black;
  color: white;
  text-align: center;
}

.footer-link {
  list-style: none;
  padding-top: 10px;
}
.footer-link a {
  text-decoration: none;
  color: white;
  margin: 0 20px;
}
.footer-container p {
  color: white;
  margin-top: 20px;
}

/* agreement page */
.agreement-cont {
  width: 100%;
  text-align: center;
  padding-top: 20px;
}

.agreement-cont h2 {
  margin-bottom: 50px;
}

.agreement-cont ul {
  text-align: left;
  width: 50%;
  margin: 0 auto;
  list-style: disc;
}

.agreement-cont li {
  padding: 8px;
}

.agreement-cont button {
  margin-top: 100px;
  padding: 10px 70px;
  border-radius: 10px;
  background: #12b612da;
  border: none;
  color: white;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 50px;
}

/* dashboard section */

.row {
  padding-left: 50px;
  padding-top: 20px;
}

/* upload section */
.upload-form {
  margin-top: 0;
}


/********************************* bootstrap*********************************************/
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  
}
.form-card {
  min-height: 100vh;
}

.agency-form-card{
  min-height: 150vh;
  margin-top: 20px;
}
.agency-form-card h2{
  text-align: center;
  margin-top: 10px;
}

.form-cards {
  min-height: 110vh;
}
.agency-form{
  top: 99%;
  left: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  min-height: 400px;
  background: #fff;
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.5);
  padding: 50px;
  box-sizing: border-box;
}
form {
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  min-height: 400px;
  background: #fff;
  box-shadow: 0 15px 50px rgba(0, 0, 0, 0.5);
  padding: 50px;
  box-sizing: border-box;
  
}
.login{
    min-height: 80vh;
}

.form h1 {
  color: #777;
  margin-bottom: 50px;
  font-size: 25px;
  font-weight: bold;
  text-align: center;
}

.form .input-box {
  position: relative;
  margin: 20px 0;
}
.form .input-box input {
  width: 100%;
  font-size: 16px;
  border: none;
  border-bottom: 2px solid #777;
  outline: none;
  padding: 10px;
  padding-left: 25px;
  box-sizing: border-box;
  font-weight: bold;
  color: #777;
}
.form .input-box input:focus,
.form .input-box input:active {
  border-bottom: 2px solid #03a9f4;
}

.form .input-box .fa {
  position: absolute;
  top: 8px;
  left: 0;
  font-size: 18px;
  color: #777;
}
.form .input-box input[type="submit"] {
  border: none;
  cursor: pointer;
  background: #03a9f4;
  font-weight: bold;
  transition: 0.5s;
  color: #fff;
  font-weight: bold;
  margin-bottom: 10px;
}
.form .input-box input[type="submit"]:hover {
  background: #ff4987;
}

.form a {
  text-decoration: none;
  color: #777;
  margin-top: 20px;
  font-weight: bold;
  display: inline-block;
  transition: 0.5s;
}
.form a:hover {
  color: #ff4987;
}


/* style for sign up  */

.sign-up{
  min-height: 100vh;
}
.signup-card{
  top: 90%;
  left: 50%;

}

